import React, { useState } from 'react';
import { Box, Button, Heading, Input, VStack, Modal, ModalOverlay, ModalContent, ModalHeader, ModalBody, ModalFooter, useDisclosure } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import { Cookies } from 'react-cookie';

const Login = () => {
  const navigate = useNavigate();
  const cookies = new Cookies();

  //  useState  username  password 
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  // 
  const [errorMessage, setErrorMessage] = useState('');

  // Chakra UI  useDisclosure Modal 
  const { isOpen, onOpen, onClose } = useDisclosure();

  const handleLogin = () => {
    //  username  password
    if (username && password) {
      if (username === 'admin' && password === 'admin') {
        //  Cookies  username
        cookies.set('username', username, { path: '/'});

        //  localStorage 
        localStorage.setItem('isLoggedIn', 'true');

        //  dashboard
        navigate('/dashboard');
      } else {
        // 
        setErrorMessage('');
        onOpen(); //  Modal
      }
    } else {
      // 
      setErrorMessage('');
      onOpen(); //  Modal
    }
  };

  //  "" 
  const handleConfirm = () => {
    onClose(); //  Modal
    navigate('/login'); // 
  };

  //  Enter 
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      e.preventDefault();  //  Enter Modal
      handleLogin();
    }
  };

  return (
    <Box w="100vw" h="100vh" display="flex" justifyContent="center" alignItems="center" bg="gray.100">
      <VStack spacing={6} boxShadow="lg" p={8} bg="white" borderRadius="md">
        <Heading size="lg" color="teal.600"></Heading>

        {/*  */}
        <Input 
          placeholder="Username" 
          value={username} 
          onChange={(e) => setUsername(e.target.value)} 
          onKeyDown={handleKeyDown}
        />

        {/*  */}
        <Input 
          placeholder="Password" 
          type="password" 
          value={password} 
          onChange={(e) => setPassword(e.target.value)} 
          onKeyDown={handleKeyDown}
        />

        {/*  */}
        <Button colorScheme="teal" width="100%" onClick={handleLogin}>
          
        </Button>
      </VStack>

      {/*  Modal */}
      <Modal isOpen={isOpen} onClose={onClose} closeOnOverlayClick={false} isCentered>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader></ModalHeader>
          <ModalBody>
            {errorMessage}
          </ModalBody>
          <ModalFooter>
            <Button colorScheme="teal" onClick={handleConfirm}>
              
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Box>
  );
};

export default Login;
